.section-portfolio {
    width: 100%;
    height: 1300px;
    display: flex;
    background-color: #161616;




    .portfolio {
        display: flex;
        justify-content: flex-start;


        .number {
            h1 {
                font-family: 'Roboto Mono', monospace;
                color: #4BFFA5;
            }
        }

        .text {
            width: 100%;
            margin-left: 110px;


            h1 {
                color: #fff;
                font-family: 'Roboto Mono', monospace;
            }

            span {
                display: flex;
                justify-content: flex-start;

                .icon {
                    width: 1rem;
                    color: #4BFFA5;
                    margin-top: 60px;
                }

                p {
                    width: 71%;
                    margin-top: 59px;
                    margin-left: 17px;
                    color: #989898;
                    font-family: 'Space Grotesk', sans-serif;
                    line-height: 1;

                    .main-text {
                        color: #fff;
                        font-size: 15px;
                        font-family: 'Space Grotesk', sans-serif;
                    }

                    .sentence {
                        width: 139%;
                    }
                }
            }

            .pictures {
                width: 95%;
                height: auto;
                margin-top: 70px;


                .up {
                    display: flex;
                    justify-content: space-between;
                    padding-left: 0px;


                    li {
                        list-style: none;

                        img {
                            width: 272px;
                            max-width: 100%;
                            height: 250px;
                            transition-duration: 2s;

                            &:hover {
                                transform: scale(1.1);
                                transition: 2s;
                            }
                        }

                        h4 {
                            color: #fff;
                            font-family: 'Space Grotesk', sans-serif;
                        }

                        p {
                            color: #717171;
                            font-family: 'Roboto Mono', monospace;
                            word-spacing: 12px;
                        }
                    }
                }

                .down {
                    display: flex;
                    justify-content: space-between;
                    padding-left: 2px;
                    margin-top: 100px;

                    li {
                        list-style: none;

                        img {
                            width: 272px;
                            max-width: 100%;
                            height: auto;
                            transition-duration: 2s;

                            &:hover {
                                transform: scale(1.1);
                                transition: 2s;
                            }
                        }

                        h4 {
                            color: #fff;
                            font-family: 'Space Grotesk', sans-serif;
                        }

                        p {
                            color: #717171;
                            font-family: 'Roboto Mono', monospace;
                            word-spacing: 12px;
                        }
                    }
                }
            }

        }
    }
}


