.section-contact {
    width: 100%;
    height: 700px;
    display: flex;
    background-color: #161616;




    .contact {
        display: flex;
        justify-content: flex-start;


        .number {
            h1 {
                font-family: 'Roboto Mono', monospace;
                color: #4BFFA5;
            }
        }

        .text {
            width: 100%;
            margin-left: 110px;


            h1 {
                color: #fff;
                font-family: 'Roboto Mono', monospace;
            }

            span {
                display: flex;
                justify-content: flex-start;

                .icon {
                    width: 1rem;
                    color: #4BFFA5;
                    margin-top: 60px;
                }

                p {
                    width: 71%;
                    margin-top: 55px;
                    margin-left: 17px;
                    color: #989898;
                    font-family: 'Space Grotesk', sans-serif;
                    line-height: 1.6;

                    .main-text {
                        color: #fff;
                        font-size: 15px;
                        font-family: 'Space Grotesk', sans-serif;
                    }

                    .sentence {
                        width: 139%;
                    }
                }
            }


            form {
                width: 58%;
                height: 400px;
                display: flex;
                justify-content: space-around;
                flex-direction: column;
                margin-top: 60px;

                .name {
                    display: flex;
                    justify-content: space-between;

                    input {
                        width: 260px;
                        height: 50px;
                        background-color: #222222;
                        border: none;
                        color: #fff;

                    }

                    ::placeholder {
                        color: #4A4A4A;
                        font-family: 'Space Grotesk', sans-serif;
                        padding-left: 20px;
                    }


                    input:focus {
                        outline: none;
                        color: #fff;
                        padding-left: 20px;

                    }

                    :focus::placeholder {
                        color: #222222;
                    }



                }

                .mail {
                    display: flex;
                    height: 226px;
                    flex-direction: column;
                    justify-content: space-between;

                    .e-mail {
                        width: 550px;
                        height: 50px;
                        background-color: #222222;
                        border: none;
                        color: #fff;
                    }

                    ::placeholder {
                        color: #4A4A4A;
                        font-family: 'Space Grotesk', sans-serif;
                        padding-left: 20px;
                    }


                    .e-mail:focus {
                        outline: none;
                        color: #fff;
                        padding-left: 20px;

                    }

                    :focus::placeholder {
                        color: #222222;
                    }

                    .message {
                        width: 550px;
                        height: 148px;
                        background-color: #222222;
                        border: none;
                        color: #fff;
                    }

                    .message::placeholder {
                        color: #4A4A4A;
                        font-family: 'Space Grotesk', sans-serif;
                        padding-left: 20px;
                        position: relative;
                        bottom: 50px;
                    }


                    .message:focus {
                        outline: none;
                        color: #fff;
                        padding-left: 20px;

                    }

                    :focus::placeholder {
                        color: #222222;
                    }

                }

            }



        }

        .btn {
            position: relative;
            display: block;
            overflow: hidden;
            width: 97px;
            height: 46px;
            background-color: transparent;
            border: 1px solid #4BFFA5;
            color: #4BFFA5;
            font-family: 'Roboto Mono', monospace;
            margin-top: 10px;
            cursor: none;
            transition: all 500ms ease;
            z-index: 0;

            &::before {
                content: "";
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                margin: auto;
                background-color: #4BFFA5;
                transition: all 500ms ease;
                z-index: -1;
            }


            &--1::before {
                width: 0%;
                height: 100%;
            }

            &--1:hover {
                color: rgb(10, 10, 10);

                &::before {
                    width: 100%;
                }
            }
        }
    }
}


